import React from "react";
import {Field, Form, Formik} from "formik";
import {login, logout} from "../../redux/header-reducer";
import {connect} from "react-redux";
import {Redirect} from "react-router-dom";
import classes from './Login.module.css'

const LoginForm = (props) => {
    return <div className={classes.form}>
        <Formik
            initialValues={{
                login: '',
                password: ''
            }}
            onSubmit={values => {
                props.login(values.login, values.password)
            }}>
            <Form className={classes.container}>
                <h1>Sign Up</h1>
                login: free@samuraijs.com pass: free
                <Field id="login" name="login" placeholder="Login" className={classes.input}/>
                <Field id="password" name="password" placeholder="********" type="password" className={classes.input}/>
                <button type="submit" className={classes.btn}>Submit</button>
            </Form>
        </Formik>
    </div>
}

const Login = (props) => {
    return (props.isAuth)
        ? <Redirect to={'/profile'}/>
        :
        <div>
            <LoginForm login={props.login}/>
        </div>

}
const mapStateToProps = (state) => {
    return {
        isAuth: state.authUser.isAuth,
    }
}
export default connect(mapStateToProps, {login, logout})(Login)