import {authAPI, profileAPI} from "../dal/api";
import {authMe, initialApp, initializeApp} from "./app-reducer";


let initialState = {
    email: null,
    id: null,
    login: null,
    isAuth: false,
    photo: null
};

const headerReducer = (state = initialState, action) => {
    switch (action.type){
        case 'ADD-USER-LOGIN-DATA':
            return {...state, email: action.userLoginData.email, id: action.userLoginData.id, login: action.userLoginData.login, isAuth: true};
        case 'ADD-USER-PHOTO':
            return {...state, photo: action.userPhoto}
        case 'TOGGLE-IS-AUTH':
            return {...state, isAuth: action.value}
        default: return state;
    }
}
export const addUserLoginData = (userLoginData) => {
    return {type: 'ADD-USER-LOGIN-DATA', userLoginData}
}
export const addUserPhoto = (userPhoto) => {
    return {type: 'ADD-USER-PHOTO', userPhoto}
}
export const isAuthToggle = (value) => {
    return {type: 'TOGGLE-IS-AUTH', value}
}

export const getAuthUserPhoto = (id) => {
    return (dispatch) => {
        profileAPI.GetUserProfile(id).then(data => {
            dispatch(addUserPhoto(data))
        })
    }
}
export const login = (email, password, rememberMe) => (dispatch) => {
    return authAPI.login(email, password, rememberMe).then(response => {
        if (response.data.resultCode === 0){
            let promise = dispatch(authMe());
            Promise.all([promise]).then(()=>{

                dispatch(isAuthToggle(true))
            })
        }
    })
}
export const logout = () => (dispatch) => {
    return authAPI.logout().then(response => {
        if (response.data.resultCode === 0){
            dispatch(isAuthToggle(false))
        }
    })
}



export default headerReducer;